function QueratorAI(props) {
  const showTutorialModal = useSelector((state) => state.showTextSlider);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(resetGeneratedContent());
  });

  return (
    <Container id="querator-ai">
      {isMobile() ? (
        <>
          <h1 className="text-center">OOPS!</h1>
          <hr />
          <p>
            We do not support mobile devices yet. Please use Desktop or Laptop
            to get the best experience
          </p>
          <p>我們還沒有支援行動裝置，請使用電腦來獲得最佳體驗</p>
        </>
      ) : (
        <>
          <h1 className="text-center">Querator AI</h1>
          <PA />
          <hr />
          <QG />
        </>
      )}
      <TutorialModal
        content={tutorial}
        show={showTutorialModal}
        onHide={() => dispatch(showTextSlider(false))}
      />
    </Container>
  );
}

export default QueratorAI;
